import React from "react"
import './App.css'
class App extends React.Component{
  constructor(){
    super();
    this.state ={
      username :" ",
      gmail :" ",
      phone :" ",
      usernameError:" ",
      gmailError :" ",
      phonenoError :" ",
    }
  }
  valid(){ 
    if(this.state.username.length < 3 && !this.state.gmail.includes("@") && this.state.phone.length < 10){
    this.setState({usernameError:"name is not valid",gmailError:"invalid mail account",phonenoError:"invalid phone number"})
  }
  else if(this.state.username.length < 3 ){
      this.setState({usernameError:"name is not valid"})
    }
    else if(!this.state.gmail.includes("@")){
      this.setState({gmailError:"invalid mail account"})
    }
    else if(this.state.phone.length < 10){
      this.setState({phonenoError:"invalid phone number"})
    }

  }
  submit(){
    if(this.valid()){
      alert("submitted");
    }
    
  }
  render(){
    return(
      
      <div className="container">
        <h1>FORM</h1>
        <label>USERNAME:<input type="text" onChange ={(event) =>{this.setState({username:event.target.value})}}/></label>
        <p>{this.state.usernameError}</p> <br/>
        <label>GMAIL:<input type ="text" onChange = {(event) =>{this.setState({gmail:event.target.value})}}/></label>
        <p>{this.state.gmailError}</p> <br/>
        <label>PHONE:<input type ="text" onChange = {(event) =>{this.setState({phone:event.target.value})}}/></label>
        <p>{this.state.phonenoError}</p>
        <button onClick = {() => this.submit()}>SUBMIT</button>
      </div>
    )
  }
}
export default App;
